import { Directive, Input, Output, EventEmitter } from '@angular/core';
import { HostListener } from '@angular/core';
import { NFileIOService } from '../services/n-fileIO.service';
import { NDirectiveOptions } from './directive.class';


declare const navigator: any;

@Directive({
  selector: '[n-shake]'
})
export class NShakeDirective {
  @Input() shakeOptions: NDirectiveOptions;
  @Output() onsuccess: EventEmitter<any> = new EventEmitter();
  @Output() onerror: EventEmitter<any> = new EventEmitter();

  constructor(private fsv: NFileIOService) { }

  @HostListener('click') methodToHandleMouseClickAction() {
    this.getShake()
      .then(sucess => this.onsuccess.emit(sucess))
      .catch(error => this.onerror.emit(error));
  }

  getShake(): Promise<any> {
    return new Promise((resolve, reject) => {
      this.fsv.getShake(this.shakeOptions).then(res => {
        return resolve(res);
      }).catch(err => reject(err));
    });
  }
}
