import { Component } from '@angular/core';
import { MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'n-alert-dialog',
  templateUrl: './n-alert.template.html',
  styleUrls: ['./n-alert.style.scss']
})

export class NAlertComponent {
  static title = '';
  static message = '';
  messageContent;
  titleContent;
  constructor(public dialogRef?: MatDialogRef<NAlertComponent>) {
    this.messageContent = NAlertComponent.message;
    this.titleContent = NAlertComponent.title;
  }

}
